.wrapper{
    width: 100%;
    background: #fff;
    padding: 10px;
    border-radius: 5px;
}
.wrapper .title{
    font-family: GilroyHome;
    position: relative;
    width: 100%;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.02em;
    color: #464754;
    margin-bottom: 30px;
}
.wrapper form .user-details{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 20px 0 12px 0;
    color: #807373;
}
form .user-details .input-box{
    margin-bottom: 15px;
    width: calc(100% / 2 - 20px);
}
form .user-details .input-box-full{
    margin-bottom: 15px;
    width: calc(100%);
}
.user-details .input-box input, .user-details .input-box-full input{
    height: 45px;
    width: 100%;
}
.user-details .input-box .details, .user-details .input-box-full .details{
    font-weight: 500;
    display: block;
    margin-bottom: 10px;
}
.user-details .input-box input{
    font-family: GilroyHome;
    height: 45px;
    width: 100%;
    outline: none;
